
*{
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	font-family: "Josefine Sans", "Arial Narrow";
}
.header{
	width: 100%;
	line-height: 100px;
	background-color: #f5ba13;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding-left: 50px;
	color: white;
	box-shadow: 5px 3px 15px -5px rgba(0, 0, 0, 1)

}

.main_note{
	width: 360px;
	padding: 15px 10px 5px 10px;
	margin: 20px auto;
	box-shadow: 5px 10px 18px #888888;
}
.my_notes h1{
	color: #34495e;
}

.my_notes p {
	color: grey;
}

form{
	min-height: 50px;
	color: #ecf0f1;
	padding: 15px 10px 5px 10px;
	box-shadow: 3px 5px 15px -5px rgba(0, 0, 0, 1);
	border-radius: 10px;
	position: relative;
}


input{
	width: 100%;
	line-height: 30px;
	background: transparent;
	border: none;
	outline: none;
	font-size: 1.5rem;
	margin: 10px 0px;
	font-weight: bold;
}

textarea{
	width: 100%;
	line-height: 30px;
	background: transparent;
	border: none;
	outline: none;
	font-size: 1rem;
	margin: 10px 0px;
}

.MuiButton-root{
	width: 30px;
	height: 50px;
	position: absolute;
	bottom: -28px;
	left: 295px;
	cursor: pointer;
	box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 1);
	background-color: #fff !important;
}

.MuiButton-root .plus_sign{
	font-size: 45px !important;
	font-weight: 900;
	color: #f4b400;
	cursor: pointer;
	z-index: 222;
	background: transparent;
}

.MuiButton-root:hover{
	background: #f4b400 !important;
}

.MuiButton-root:hover .plus_sign{
	color: #fff;
	background: transparent;
}
.note{
	background: #fff;
	border-radius: 7px;
	box-shadow: 0px 2px 5px #ccc;
	padding: 10px;
	width: 240px;
	margin: 16px;
	float: left;
}

.note h1{
	font-size: 1.1em;
	margin-bottom: 6px;
}
.note p{
	font-size: 1.1em;
	margin-bottom: 10px;
	white-space: pre-wrap;
	word-wrap: break-word;
}

.note .btn{
	position: relative;
	float: right;
	margin-right: 10px;
	color: #f4b400;
	border: none;
	width: 50px;
	height: 50px;
	background: transparent;
	cursor: pointer;
	outline: none;
	box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 1);
	border-radius: 50%;
}


.deleteicon{
	width:  0.7rem;
	height: 0.7rem;
}

.note .btn:hover{
	background-color: #f5ba13;
}

.note .btn:hover .deleteicon{
	color: #fff;
}

